// @ts-ignore
import Log from '@zhigamovsky/styled-console-log'
import React, { useEffect } from 'react'
import styled from 'styled-components'

import { ITableIntervalContent } from './interfaces'
import Calendar from 'react-calendar';

const TableIntervalContent: ITableIntervalContent = ({
  dateFrom, dateTo, onChange
}) => {

  useEffect(() => {
    Log.dotbrown('<TableIntervalContent /> HAS BEEN SUCCESSFULLY RENDERED');
  }, [])

  return (
    <Container>
      <Calendar
        onChange={onChange}
        value={[
          dateFrom,
          dateTo
        ]}
        calendarType='ISO 8601'
        className='table-interval-content'
        tileClassName='tile-table-interval-content'
        selectRange={true}
      />
    </Container>
  )
}

const Container = styled.div`

`;

export default TableIntervalContent;
