// @ts-ignore
import Log from '@zhigamovsky/styled-console-log'
import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

Log.addOwnTemplate('dotbrown')('⚒')('padding: 4px; border: 1px dotted #ed8864;  background: rgba(121, 85, 72, 0.2); color: #ed8864;');
Log.addOwnTemplate('dotblue')('⚒')('padding: 4px; border: 1px dotted #4c4cff;  background: #00006620; color: #4c4cff;');
Log.addOwnTemplate('dotyellow')('⚒')('padding: 4px; border: 1px dotted #ffff00;  background: #ffff0020; color: #ffff00;');

ReactDOM.render(
  <React.Fragment>
    <App />
  </React.Fragment>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
