// @ts-ignore 
import Log from '@zhigamovsky/styled-console-log';
import React, { useState, forwardRef, useImperativeHandle } from 'react';
import styled from 'styled-components';
import { Bar } from 'react-chartjs-2';

import * as Piece from '../';
import { flex, colors as $colors } from '../../utils/css';
import { getOffsetDivWidth } from '../../utils/chart';

const ChartElement: any = forwardRef((props, ref) => {
  const Labels: string[] = ['00', '06', '12', '18', '24'];
  const [data, updateData] = useState<any>(null);

  let fullChartOptions = {
    legend: { display: false },
    title: {
      display: false,
    },
    layout: {
      padding: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0
      }
    },
    scales: {
      xAxes: [{
        gridLines:{
          drawBorder: true,
          zeroLineColor: 'rgba(255, 255, 255, 0.05)',
          lineWidth: 0,
          zeroLineWidth: 1
        },
        ticks: {
          display: true,
        },
      }],
      yAxes: [{
        gridLines: {
          drawBorder: true,
          zeroLineColor: 'rgba(255, 255, 255, 0.05)',
          lineWidth: 0,
          zeroLineWidth: 1
        },
        ticks: {
          display: true,
          maxTicksLimit: 4,
          beginAtZero: true,
          fontFamily: 'Montserrat',
          fontSize: 10,
          fontColor: '#BDBDBD',
          padding: 0,
          textAlign: 'left',
          callback: (value: any, index: any, values: any) => {
            value = value.toString();
            let maxLength = 4;
            Object.keys(values).map(v => maxLength = Math.max(maxLength, v.toString().length))
            return value + ' '.repeat(maxLength - value.length + 2);
          }
        }
      }]
    },
  }

  const generateRandomChart = (updated_data: any): void => {
    updated_data = {
      data: updateData,
      date: new Date()
    };
    
    const length = 26;
    const maxValue = Math.ceil(101 + 300 * Math.random());
    const redValueAfter =  maxValue / 1.75;    
    const datas = [];
    const colors = [];    
    for (let i = 0; i < length; i++) {
      const v = maxValue * Math.random();
      datas.push(v);
      if (v > redValueAfter) {
        colors.push($colors.redwave);
      } else {
        colors.push($colors.bluewave);
      }
    }

    let new_chart_datasets = {
      labels: ' '.repeat(colors.length).split(' '),
      datasets: [{
        backgroundColor: colors,
        barPercentage: 1,
        barThickness: (getOffsetDivWidth('marked-chart-for-getting-width').width || 290) / 58 || 5,
        maxBarThickness: 10,
        borderWidth: 0,
        strokeColor: 'rgba(255,99,132,0.2)',
        hoverBackgroundColor: 'rgba(255,99,132,0.4)',
        hoverBorderColor: 'rgba(255,99,132,1)',
        colors: colors,
        data: datas
      }]
    }

    updateData(new_chart_datasets);
    Log.success('Success generate Chart', new_chart_datasets);
  }

  useImperativeHandle(ref, () => ({
    updateChart(data: any) {
      generateRandomChart(data);
    }
  }));

  return (
    <Container>
      {
        data ?
          <Bar
            data={data}
            width={getOffsetDivWidth('marked-chart-for-getting-width').width || 0}
            height={174}
            options={fullChartOptions}
          /> 
        : null
      }
      <LabelsContainer>
        {
          Labels.map(label => {
            return (
              <Piece.Text
                key={label}
                size={7}
                family='Montserrat'
                color='#BDBDBD'
              >{label}</Piece.Text>
            )
          })
        }
      </LabelsContainer>
    </Container>
  )
});

const Container = styled.div` `;

const LabelsContainer = styled.div`
  margin-top: -20px;
  width: 100%;
  padding-left: 40px;
  ${flex('row', 'center','space-around')}
`;

export default ChartElement;