// @ts-ignore 
import Log from '@zhigamovsky/styled-console-log'
import React, { TouchEvent, useEffect, useState } from 'react'
import styled from 'styled-components'

import * as Piece from '../'
import { IRangeInput } from './interfaces'
import { flex } from '../../utils/css'

interface IDimmension {
  height?: number,
  width: number,
}

interface ISyntheticCursorEventHandler {
  onTouchCancel: {
    (event: TouchEvent<HTMLDivElement>): void
  },
  onTouchEnd: {
    (event: TouchEvent<HTMLDivElement>): void
  },
  onTouchStart: {
    (event: TouchEvent<HTMLDivElement>): void
  },
  onTouchMove: {
    (event: TouchEvent<HTMLDivElement>): void
  }
}

const RangeInput: IRangeInput = ({onChange, value, valueList}) => {
  const BorderCursorDimmenstion: IDimmension = {
    width: 6
  }

  const diapazon: number[] = [4, 24, 48, 72, 94];

  const CursorDimmension: IDimmension = {
    height: 30,
    width: 30
  }

  const [coefficient, setCoefficient] = useState<number>(94);

  const [touchStartPosition, setTouchStartPosition] = useState<number>(0);
//   const [touchEndPosition, setTouchEndPosition] = useState<number>(0);

  const parseIntoCoefficient = (): void => {
    // let diapazon: number[] = [3, 22, 41, 60, 78, 96];
    let deccoef = 100 / valueList.length;
    let c = (CursorDimmension.width / 2) + (BorderCursorDimmenstion.width);
    let coef = CursorDimmension.width / valueList.length / 2;
    Log.info('Divide', {
        deccoef, c , coef
    });
  }

  const getIndexElement = (): number => {
    return valueList.findIndex(v => v.slug === value.slug)
  }

  const getElementDiapazon = (): void => {
    setCoefficient(diapazon[getIndexElement()])
  }

  const getTouchDirection = (e: TouchEvent<HTMLDivElement>): 'down' | 'up' | undefined => {
    // const te = e.changedTouches[0].clientY;
    const touchEndPosition = e.changedTouches[0].clientY
    if(touchStartPosition < touchEndPosition + 5){
       return 'down'
    } else if(touchStartPosition > touchEndPosition - 5){
       return 'up'
    }
    // setTouchStartPosition()
  }

  const changeRange = (e: TouchEvent<HTMLDivElement>, eventType: string): void => {
    let direction = getTouchDirection(e);
    let index = getIndexElement();
    Log.dotyellow(eventType, direction);
    if(direction === 'down') {
      if(index === diapazon.length - 1) {
        Log.light('d - '+direction+', i - '+index + ', l - ' + (diapazon.length - 1) + '. Is End!' )
      }
      else {
        // Log.light(valueList[index+1] + 'd - '+direction+', i - '+index + ', l - ' + (diapazon.length - 1))
        onChange(valueList[index+1])
      }
    }
    else {
      if(index === 0) {
        Log.light('d - '+direction+', i - '+index + ', l - ' + (diapazon.length - 1) + '. Is Begin!' )
      }
      else {
        // Log.light(valueList[index+1] + 'd - '+direction+', i - '+index + ', l - ' + (diapazon.length - 1))
        onChange(valueList[index-1])
      }
    }
  }

  useEffect(() => {
    parseIntoCoefficient()
  }, [])

  useEffect(() => {
    getElementDiapazon();
  }, [value])

  const SyntheticCursorEventHandler: ISyntheticCursorEventHandler = {
    onTouchCancel: e => {
      changeRange(e, 'onTouchCancel()');
    },
    onTouchEnd: e => {
      changeRange(e, 'onTouchEnd()');
    },
    onTouchMove: e => {
      
    },
    onTouchStart: e => {
      Log.dotyellow('Synthetic Cursor Event onTouchStart()', e);
      setTouchStartPosition(e.touches[0].clientY);
    }
  }

  return (
    <Container>
      <VerticalRange>
        <Cursor
          dimmesions={{
            size: CursorDimmension,
            border: BorderCursorDimmenstion,
            coefficient
          }}
          onTouchCancel={SyntheticCursorEventHandler.onTouchCancel}
          onTouchEnd={SyntheticCursorEventHandler.onTouchEnd}
          onTouchMove={SyntheticCursorEventHandler.onTouchMove}
          onTouchStart={SyntheticCursorEventHandler.onTouchStart}
        >
          <Piece.Text 
            size={9} 
            color='#27262E'
            justify='center'
            align='center'
          >
            {value.value}
          </Piece.Text> 
        </Cursor>
      </VerticalRange>
    </Container>
  )
}

const Container = styled.div`
  width: 100%;
  height: 100%;
  ${flex()}
`;

const VerticalRange = styled.div`
  width: 10%;
  height: 100%;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 50px;
  position: relative;
`;

const Cursor = styled.div<{
  dimmesions: {
    size: IDimmension,
    border: IDimmension,
    coefficient: number
  }
}>`
  touch-action: none;
  background: white;
  border-radius: 50%;
  ${({dimmesions: D}) => `
    width: ${D.size.width}px;
    height: ${D.size.height}px;
    margin-top: -${(D.size.width / 2) + (D.border.width)}px;
    border: ${D.border.width}px solid #2B2A30;
    margin-left: -${(D.size.width / 2) + (D.border.width)}px;
    top: ${D.coefficient}%;
  `}
  position: absolute;
  left: 50%;
  ${flex()}
  transition: 0.2s ease;
  &:active {
    transform: scale(1.1);
  }
`;

export default RangeInput;