interface IUGetOffsetDivWedth {
  (
    classname: string
  ): {
    element: Element | null,
    width: number | undefined,
    height: number | undefined
  }
}

interface IUGetSummuryOffsetDivWedth {
  (
    classname: string
  ): {
    elements: Element[] | null,
    summuryWidth: number | undefined,
    summuryHeight: number | undefined
  }
}

export const getOffsetDivWidth: IUGetOffsetDivWedth = (classname) => {
  let element: Element | undefined = document.getElementsByClassName(classname)[0];
  return {
    element,
    width: element?.clientWidth,
    height: element?.clientHeight
  }
}

export const getSummuryOffsetDivWidth: IUGetSummuryOffsetDivWedth = (classname) => {
  let elements: Element[] = Array.from(document.getElementsByClassName(classname));
  // console.log('Some[1]', elements.map(e => (e.clientHeight)))
  // console.log('Some[2]', elements.map(e => (e.clientHeight)).reduce((accumulator = 0, currentValue) => accumulator + currentValue, 0))
  let summuryWidth: number = elements.map(e => (e.clientWidth)).reduce((accumulator, currentValue) => accumulator + currentValue, 0);
  let summuryHeight: number = elements.map(e => (e.clientHeight)).reduce((accumulator, currentValue) => accumulator + currentValue, 0);
  return {
    elements,
    summuryWidth,
    summuryHeight
  }
}