// @ts-ignore
import Log from '@zhigamovsky/styled-console-log'
import React, { useState } from 'react'
import styled from 'styled-components'

import * as Piece from '..'
import * as Modal from './'

import { ICheckBoxGroup } from './interfaces'
import { flex } from '../../utils/css'
 
const CheckBoxGroup: ICheckBoxGroup = ({value, onChange}) => {
  const [checkboxValues, changeCheckboxValues] = useState<any[]>(value);
  
  const handleChangGroupCheboxes = (id: number, value: boolean) => {
    let isExistIndex = checkboxValues.findIndex(c => c.id === id);
    let state: any[] = [...checkboxValues];
    if(isExistIndex + 1){
      state[isExistIndex].value = value;
      changeCheckboxValues(state);
      onChange(state)
      Log.error('handleChangeGroupCheckboxes', {
        id, value, isExistIndex, state, checkboxValues
      });
    }
  }

  return (
    <div>
      {
        checkboxValues.map(q => {
          return (
            <Row key={q.id}>
              <Modal.CheckBox 
                value={q.value}
                onChange={(v) => handleChangGroupCheboxes(q.id, v)}
                isInverted={q.isInverted}
              />
              <Piece.Text
                size={8}
                containerStyles='padding-left: 10px;'
                onClick={() => handleChangGroupCheboxes(q.id, !q.value)}
              >
                {q.title}
              </Piece.Text>
            </Row>
          )
        })
      }
    </div>
  )
}

const Row = styled.div`
  padding: 10px 15px;
  ${flex('row', 'center', 'flex-start')}
  width: calc(100% - 30px);
  border-bottom: 1px solid #37363E;
`;

export default CheckBoxGroup;