import React, { useState, forwardRef, useImperativeHandle } from 'react';
import styled from 'styled-components';
import { Bar } from 'react-chartjs-2';

import { colors as $colors } from '../../utils/css';
import { getOffsetDivWidth } from '../../utils/chart';

const ChartMinifiedElement: any = forwardRef((props, ref) => {
  const [data, updateData] = useState<any>(null);
  const generateRandomChart = (updated_data: any): void => {
    updated_data = {
      data: updateData,
      date: new Date()
    };
    const length = 26; 
    const maxValue = Math.ceil(101 + 300 * Math.random()); 
    const datas = [];
    const colors = [];
    for (let i = 0; i < length; i++) {
      const v = maxValue * Math.random();
      datas.push(v);
      colors.push($colors.white)
    }

    let new_chart_datasets = {
      labels: ' '.repeat(colors.length).split(' '),
      datasets: [{
        backgroundColor: colors,
        barPercentage: 1,
        barThickness: (getOffsetDivWidth('marked-chart-for-getting-width').width || 290) / (58 * 1.5) || 5,
        maxBarThickness: 10,
        borderWidth: 0,
        strokeColor: 'rgba(255,99,132,0.2)',
        hoverBackgroundColor: 'rgba(255,99,132,0.4)',
        hoverBorderColor: 'rgba(255,99,132,1)',
        colors: colors,
        data: datas
      }]
    }
    updateData(new_chart_datasets)
  }

  let options = {
    legend: { display: false },
    title: {
      display: false,
    },

    scales: {
      xAxes: [{
        gridLines: false,
        ticks: {
          display: false,
          beginAtZero: true,
        }
      }],
      yAxes: [{
        gridLines: false,
        ticks: {
          display: false,
          beginAtZero: true
        }
      }]
    },
  };

  useImperativeHandle(ref, () => ({
    updateChart(data: any): void {
      generateRandomChart(data);
    }
  }));

  return (
    <Container>
      {
        data ?
        <Bar
          data={data}
          width={(getOffsetDivWidth('marked-chart-for-getting-width').width  || 0) / 1.5 }
          height={45}
          options={options}
        /> : null
      }
    </Container>
  )
})

const Container = styled.div` `;

export default ChartMinifiedElement;