// @ts-ignore
import Log from '@zhigamovsky/styled-console-log'
import React, { useEffect, useState } from 'react';
import styled from 'styled-components'

import * as Piece from './components';
import { flex, colors } from './utils/css';

import { TPagger } from './interfaces/general';

const App = () => {
  const [activePage, toggleActivePage] = useState<TPagger>('auth')
  
  useEffect(() => {
    Log.dotbrown('Application HAS BEEN RENDERED');
  }, []);

  return (
    <Application>
      <Piece.AuthorizationModalWindow
        key='auth'
        pageController={{
          activePage, 
          isSelfActive: activePage === 'auth',
          toggler: () => toggleActivePage('grid')
        }} 
      />
      <Piece.LongGridContent 
        key='grid'
        pageController={{
          activePage, 
          isSelfActive: activePage === 'grid',
          toggler: () => toggleActivePage('auth')
        }} 
      />
    </Application>
  );
}

const Application = styled.div`
  height: 100%;
  position: absolute;
  overflow: hidden;
  width: 100%;
  background: ${colors._grey.darkbg};
  color: ${colors.white};
  ${flex('column')}
`;

export default App;