// @ts-ignore 
import Log from '@zhigamovsky/styled-console-log'
import React, { useEffect } from 'react'
import styled from 'styled-components'

import * as Piece from '../'
import * as Grid from './'
import { IGridGroup } from './interfaces'

const GridGroup: IGridGroup = ({globalTitle, items}) => {
  useEffect(() => {
    Log.dotbrown('GridGroup HAS BEEN RENDERED!');
  }, []);
  
  return (
    <Container>
      <Piece.Text align="flex-start">{globalTitle}</Piece.Text>
      <Piece.Divider height={20} />
      {
        items.map((item: any) => {
          return (
            <React.Fragment 
              key={item.id}
            >
              <Grid.CollapseItem
                title={item.title}
                measure={item.measure}
                chartData={item.chartData}
                mode={item.mode}
                isBackgroundWarn={item.isBackgroundWarn}
                isBorderWarn={item.isBorderWarn}
                isInterval={item.isInterval}
                intervalDuration={item.intervalDuration}
              />
              <Piece.Divider height={15} />
            </React.Fragment>
          )
        })
      }
    </Container>
  )
}

const Container = styled.div`
  width: 94%;
  margin: 0 3%;
`;

export default GridGroup;