import { IFonts, IColors, IFontController, IFlexController } from "../interfaces/css";

export const font: IFontController = (type = 'Roboto', size = 20, color = '#333333'): string => {
  return `
    font-family: ${type};
    font-size: ${size}pt;
    color: ${color};
  `;
}
    
export const flex: IFlexController = (direction = 'row', align = 'center', justify = 'center') => {
  return `
    display: flex;
    flex-direction: ${direction};
    align-items: ${align};
    justify-content: ${justify};
  `;
}

export const fonts: IFonts = {
  Montserrat: 'Montserrat',
  Roboto: 'Roboto'
}

