import { keyframes } from 'styled-components'

export const invalidFormEffect = keyframes`
  0% {
    margin-left: 5%;
    margin-right: 5%;
  }
  25% {
    margin-left: calc(5% - 10px);
    margin-right: calc(5% + 10px);
  }
  50% {
    margin-left: calc(5% + 10px);
    margin-right: calc(5% - 10px);
  }
  75%{
    margin-left: calc(5% - 5px);
    margin-right: calc(5% + 5px);
  }
  88%{
    margin-left: calc(5% + 5px);
    margin-right: calc(5% - 5px);
  }
  100% {
    margin-left: 5%;
    margin-right: 5%;
  }
`;

export const disappeareFormEffect = keyframes`
  0% {
    margin-bottom: 0;
  }
  90% {
    opacity: 1;
  }
  100% {
    margin-top: -2000px;
    opacity: 0;
  }
`;

export const apearanceLongGridEffect = keyframes`
  0% {
    margin-bottom: -2000px;
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  100% {
    margin-bottom: 0;
    margin-top: 0;
    opacity: 1;
  }
`;