export type TExtendedDate = {
  d: number,
  dd: string,
  m: number,
  mm: string,
  mmm: string,
  y: number,
  yy: number
}

export type TExtendedTime = {
  h: number,
  hh: string,
  m: number,
  mm: string,
  s: number,
  ss: string,
}

export type TDateSingleton = {
  date: TExtendedDate,
  time: TExtendedTime,
  iso: string,
  constructor: Date
}

export type TDateInObjectFormat = {
  from: TDateSingleton,
  to: TDateSingleton
}

export const initialDateInObjectFormat: TDateInObjectFormat = {
  from: {
    date: {
      d: -1,
      dd: '-1',
      m: -1,
      mm: '-1',
      mmm: '-1',
      y: -1,
      yy: -1
    },
    time: {
      h: -1,
      hh: '-1',
      m: -1,
      mm: '-1',
      s: -1,
      ss: '-1'
    },
    iso: new Date().toISOString(),
    constructor: new Date()
  },
  to: {
    date: {
      d: -1,
      dd: '-1',
      m: -1,
      mm: '-1',
      mmm: '-1',
      y: -1,
      yy: -1
    },
    time: {
      h: -1,
      hh: '-1',
      m: -1,
      mm: '-1',
      s: -1,
      ss: '-1'
    },
    iso: new Date().toISOString(),
    constructor: new Date()
  }
}

export const parseDateInObjectFormat = (date: Date): TDateSingleton => {
  let _d = date.getDay();
  let _m = date.getMonth();
  let _y = date.getFullYear();
  let _hh = date.getHours();
  let _mm = date.getMinutes();
  let _ss = date.getSeconds();
  
  const prepareDefaulPadStart = (value: number): string => {
    return value.toString().padStart(2, '0')
  }

  return {
    date: {
      d: _d,
      dd: prepareDefaulPadStart(_d),
      m: _m,
      mm: prepareDefaulPadStart(_m),
      mmm: monthMapConfigGenitive[_m + 1],
      y: _y,
      yy: _y
    },
    time: {
      h: _hh,
      hh: prepareDefaulPadStart(_hh),
      m: _mm,
      mm: prepareDefaulPadStart(_mm),
      s: _ss,
      ss: prepareDefaulPadStart(_ss)
    },
    iso: date.toISOString(),
    constructor: date
  }
}

