import React from 'react';
import styled from 'styled-components'
import { colors, flex, font } from '../utils/css';
import { TAlign, TFont, TJustify } from '../interfaces/css';
import { IText } from '../interfaces/Text'

const Text: IText = ({
  size = 14, 
  family = 'Roboto', 
  color = colors.white, 
  align = 'center', 
  containerStyles = '', 
  textStyles = '', 
  justify = 'flex-start',
  children,
  onClick
}) => {
  return (
    <Container 
      align={align} 
      justify={justify} 
      innerStyles={containerStyles}
      onClick={onClick}
    >
      <SelfText 
        cssObject={{size, type: family, color}} 
        innerStyles={textStyles}
      >{children}</SelfText>
    </Container>
  )
}

const Container = styled.div<{align: TAlign, innerStyles: string, justify: TJustify}>`
  ${({align, justify}) => flex('row', align, justify)}
  width: 100%;
  ${({innerStyles}) => innerStyles}
`;

const SelfText = styled.div<{cssObject: TFont, innerStyles: string}>`
  ${({
    cssObject: { 
      type, 
      color, 
      size 
    }
  }) => font(type, size, color)}
  
  ${({
      innerStyles
    }) => innerStyles}
`;

export default Text;